<template>
  <table class="table w-100 text-start">
        <thead>
            <tr class="table-light">
                <th scope="col">Name</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="category in categories" :key="category.name" class="table-row border-1" @click="action(category.name)">
                <td>{{category.name}}</td>
            </tr>
        </tbody>
    </table>
    
</template>

<script>
import { inject, onMounted } from '@vue/runtime-core'

export default {
    name: 'CategoryList',
    props: {
        src: String
    },
    setup(props, {emit}){
        const CategoryAPI = inject('CategoryAPI')
        const {CATEGORY_LIST,SELECTED_CATEGORY_NAME} = inject('GLOBAL_CATEGORY')
        
        onMounted(async () => {
            CATEGORY_LIST.value = await CategoryAPI.getAllCategoriesList() 
        })
        
        const action  = (name) => {
            if(name){
                if(props.src !== 'product-modal'){
                    SELECTED_CATEGORY_NAME.value =  name 
                    $('#modalCategoryEdit').modal('show')
                    return
                }
                
                emit('categorySelect', name)
            }
        }

        return {
            categories: CATEGORY_LIST,
            action
        }
    }
}
</script>

