<template>
  <table class="table w-100 text-start">
        <thead>
            <tr class="table-light">
                <th scope="col">Name</th>
                <th scope="col">Price</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="product in products" :key="product.name" class="table-row border-1" @click="startEditingProduct(product.name)">
                <td>{{product.name}}</td>
                <td>{{product.price}} €</td>
            </tr>
        </tbody>
    </table>
    
</template>

<script>
import { inject, onMounted } from '@vue/runtime-core'

export default {
    setup(){
        const ProductAPI = inject('ProductAPI')
        const {PRODUCT_LIST,SELECTED_PRODUCT_NAME} = inject('GLOBAL_PRODUCT')
        
        onMounted(async () => {
            PRODUCT_LIST.value = await ProductAPI.getAllProductsList() 
        })
        
        const startEditingProduct  = (name) => {
            if(name){
                SELECTED_PRODUCT_NAME.value =  name 
                $('#modalProductEdit').modal('show')
                return
            }

        }
        return {
            products: PRODUCT_LIST,
            startEditingProduct
        }
    }
}
</script>

