<template>
    <div>
        <button class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#modalcategoryNew">
            New
        </button>
        <form class="modal" tabindex="-1" id="modalcategoryNew" autocomplete="off" @submit.prevent="action">
            <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">New category</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                            <fieldset>
                                <label for="name">Name</label><br>
                                <input type="text" id="name" name="name" v-model="category.name" required><br>
                            </fieldset>
                    </div>
                    <div class="modal-footer ">
                        <button type="button" class="btn btn-secondary" id="closeModalNew"
                            data-bs-dismiss="modal">Cancel</button>
                        <button type="submit" class="btn btn-success">Confirm</button>
                    </div>
                </div>
            </div>
        </form>
  
    </div>
</template>

<script>
import { inject, ref} from '@vue/runtime-core'

export default {
    name: 'category_new',
    setup(props, {emit}){
        const category = ref({})
        const CategoryAPI = inject('CategoryAPI');

        const action = () => {
            CategoryAPI.addCategory(category.value)
            .then(res => {
                category.value = {}
                $('#modalcategoryNew').modal('hide')
                CategoryAPI.refreshList()
                emit('success',res.data)
            })
            .catch(err => {
                emit('error',err)
            })
        }

        return {
            category,
            action
        }
    }

}
</script>

