<template>
    <div>
        <!-- <button class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#modalProductEdit">
            Edit
        </button> -->
        <form class="modal" tabindex="-1" id="modalProductEdit" autocomplete="off" @submit.prevent="editAction">
            <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">Edit Product</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                            <fieldset>
                                <label for="name">Name</label><br>
                                <input type="text" id="name" name="name" v-model="product.name" required><br>
                                <label for="price">Price</label><br>
                                <input type="text" id="price" name="price" v-model="product.price" required><br>  
                                <h4>Categories</h4>
                                <div v-for="category in product.categories" :key="category">{{category}}</div>
                            </fieldset>
                    </div>
                    <div class="modal-footer ">
                        <button type="button" class="btn btn-danger" @click="deleteAction">Delete</button>
                        <button type="button" class="btn btn-secondary" id="closeModalEdit"
                            data-bs-dismiss="modal">Cancel</button>
                            
                        <button type="submit" class="btn btn-success">Confirm</button>
                    </div>
                </div>
            </div>
        </form>
  
    </div>
</template>

<script>
import { inject, ref, watch} from '@vue/runtime-core'
export default {
    name: 'products_edit',
    setup(props, {emit}){
        const filter = ref('')
        const ProductAPI = inject('ProductAPI');
        const {SELECTED_PRODUCT_NAME} = inject('GLOBAL_PRODUCT');

        const editAction = () => {
            ProductAPI.updateProduct(filter.value,product.value)
            .then(res => {
                $('#modalProductEdit').modal('hide')
                emit('success',res.data)
            })
            .catch(err => {
                emit('error',err)
            })
        }

          const deleteAction = () => {
            ProductAPI.deleteProduct(SELECTED_PRODUCT_NAME.value)
            .then(res => {
                SELECTED_PRODUCT_NAME.value = ''
                emit('success')
            })
            .catch(err => {
                console.log(err)
            })
        }
        const product = ref({})
        
        watch(SELECTED_PRODUCT_NAME, async (name,prevName) => {
            if(name){
                // get product from server
                const res = await ProductAPI.getProduct(name)
                if(!res) console.log("[modal.vue line82] no product")

                // set modal product v-model value 
                product.value = res.data

                // save initial product filter key
                filter.value = {name:name}

            }
            console.log("[modal.vue line82] empty string")
        })

        return {
            ProductAPI,
            product,
            editAction,
            deleteAction
        }
    }

}
</script>

