<template>
    <div>
        <button class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#modalProductNew">
            New
        </button>
        <form class="modal" tabindex="-1" id="modalProductNew" autocomplete="off" @submit.prevent="action">
            <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">New Product</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                            <fieldset>
                                <label for="name">Name</label><br>
                                <input type="text" id="name" name="name" v-model="product.name" required><br>
                                <label for="price">Price</label><br>
                                <input type="text" id="price" name="price" v-model="product.price" required><br>  
                                <div class="categories-wrapper" @click="toggleSelector()">
                                <h4>Categories</h4>
                                    <div v-for="(category, index) in product.categories" :key="category">{{category + (index > 1 ? ',' : '')}}</div>
                                </div>
                                <div class="category-selector w-50 mx-auto d-none">
                                    <CategoryList :src="'product-modal'" @categorySelect="saveCategory"/>
                                </div>
                            </fieldset>
                    </div>
                    <div class="modal-footer ">
                        <button type="button" class="btn btn-secondary" id="closeModalNew"
                            data-bs-dismiss="modal">Cancel</button>
                        <button type="submit" class="btn btn-success">Confirm</button>
                    </div>
                </div>
            </div>
        </form>
  
    </div>
</template>

<script>
import { inject, ref} from '@vue/runtime-core'
import CategoryList from '@/components/admin/category/list'
  
export default {
    name: 'products_new',
      components:{
        CategoryList
    },

    setup(props, {emit}){
        const product = ref({})
        const ProductAPI = inject('ProductAPI');
        const categories = ref([]);

        const action = () => {
            product.value.categories = categories.value
            ProductAPI.addProduct(product.value)
            .then(res => {
                product.value = {}
                $('#modalProductNew').modal('hide')
                emit('success',res.data)
            })
            .catch(err => {
                emit('error',err)
            })
        }
        const saveCategory = (name) => {
            if(categories.value.indexOf(name) === -1) categories.value.push(name);
        }

        const toggleSelector = () => {
            $('.category-selector').hasClass('d-none') ? $('.category-selector').removeClass('d-none') : $('.category-selector').addClass('d-none') 
        }
        return {
            ProductAPI,
            product,
            action,
            toggleSelector,
            categories,
            saveCategory
        }
    }

}
</script>

