<template>
    <div class="d-flex flex-column">
        <Dialog></Dialog>
        <section id="SECTION_PRODUCTS_LIST" class="d-flex flex-column justify-content-center mt-3 w-100 gap-3 mt-5 rounded">
            <h4>Products</h4>
            <ProductList @success="refresh" @error="fail" />
            <ProductNew @success="refresh" @error="fail" />
            <ProductModal @success="refresh" @error="fail" />
            <button class="btn btn-danger w-25 mx-auto" @click="ProductAPI.deleteAll()">Delete all</button>

        </section>

        <section id="SECTION_CATEGORY_LIST" class="d-flex flex-column justify-content-center mt-3 w-100 gap-3 mt-5 rounded">
            <h4>Categories</h4>
            <CategoryList @success="refresh" @error="fail" />
            <CategoryNew @success="refresh" @error="fail" />
            <CategoryModal @success="refresh" @error="fail" />
            <button class="btn btn-danger w-25 mx-auto" @click="CategoryAPI.deleteAll()">Delete all</button>
        </section>

    </div>
</template>

<script>
import { inject,onMounted, ref } from '@vue/runtime-core'
import ProductNew from '@/components/admin/product/new'
import ProductModal from '@/components/admin/product/modal'
import ProductList from '@/components/admin/product/list'
import CategoryList from '@/components/admin/category/list'

import CategoryNew from '@/components/admin/category/new'
import CategoryModal from '@/components/admin/category/modal'
import { notify } from "@kyvg/vue3-notification";

export default {
    components:{
        ProductNew,
        ProductModal,
        ProductList,
        CategoryNew,
        CategoryModal,
        CategoryList
    },
    setup(){
        // Variables
        const products = ref([])
        const removeID = ref('');
        const ProductAPI = inject('ProductAPI')
        const CategoryAPI = inject('CategoryAPI')

        // Methods
        // convert refresh() & fail() into helper notification helper
        const refresh = async (msg) => {
            notify({
                title: "Done!",
                text: msg,
                type: "success"
            });
        }

        const fail = async (msg) => {
            notify({
                title: "Error",
                text: msg,
                type: "error"
            });
        }

        return {ProductAPI,CategoryAPI,removeID,refresh,fail}
    },
   
}
</script>

<style scoped>
.table-row{
    transition: background 200ms ease;
}
.table-row:hover{
    background: rgb(233, 233, 233);
    cursor: pointer;
}
</style>