<template>
    <div>
        <!-- <button class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#modalCategoryEdit">
            Edit
        </button> -->
        <form class="modal" tabindex="-1" id="modalCategoryEdit" autocomplete="off" @submit.prevent="editAction">
            <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">Edit Category</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                            <fieldset>
                                <label for="name">Name</label><br>
                                <input type="text" id="name" name="name" v-model="category.name" required><br>
                            </fieldset>
                    </div>
                    <div class="modal-footer ">
                        <button type="button" class="btn btn-danger" @click="deleteAction">Delete</button>
                        <button type="button" class="btn btn-secondary" id="closeModalEdit"
                            data-bs-dismiss="modal">Cancel</button>
                            
                        <button type="submit" class="btn btn-success">Confirm</button>
                    </div>
                </div>
            </div>
        </form>
  
    </div>
</template>

<script>
import { inject, ref, watch} from '@vue/runtime-core'
export default {
    name: 'category_edit',
    setup(props, {emit}){
        const filter = ref('')
        const CategoryAPI = inject('CategoryAPI');
        const {SELECTED_CATEGORY_NAME} = inject('GLOBAL_CATEGORY');

        const editAction = () => {
            CategoryAPI.updateCategory(filter.value,category.value)
            .then(res => {
                $('#modalCategoryEdit').modal('hide')
                emit('success',res.data)
            })
            .catch(err => {
                emit('error',err)
            })
        }

          const deleteAction = () => {
            CategoryAPI.deleteCategory(SELECTED_CATEGORY_NAME.value)
            .then(res => {
                SELECTED_CATEGORY_NAME.value = ''
                emit('success')
            })
            .catch(err => {
                console.log(err)
            })
        }
        const category = ref({})
        
        watch(SELECTED_CATEGORY_NAME, async (name,prevName) => {
            if(name){
                // get category from server
                const res = await CategoryAPI.getCategory(name)
                if(!res) console.log("[modal.vue line82] no category")

                // set modal category v-model value 
                category.value = res.data

                // save initial category filter key
                filter.value = {name:name}

            }
            console.log("[modal.vue line82] empty string")
        })

        return {
            CategoryAPI,
            category,
            editAction,
            deleteAction
        }
    }

}
</script>

